import React, { useContext } from 'react';
import { LeagueConsumer, LeagueContext } from './index.js';
import styled from 'styled-components';

const FinalTotal = (props) => {
	const context = useContext(LeagueContext);
	const { state, set } = context;
	const { currentTotal } = state;

	return <LeagueConsumer>
		{(context) => (
			<TotalsWrapper>
				<Header>Total: {currentTotal}</Header>
			</TotalsWrapper>
		)}
	</LeagueConsumer>
}

const TotalsWrapper = styled.div`
	width: 360px;
	display: flex;
	justify-content: center;
	margin: 0 auto;
	padding: 0;
	font-size: 1.2em;
	color: rgba(255, 255, 255, 0.7);
	background-color: rgba(40,65,78,1);
`

const Header = styled.span`
	text-align: center;
	display: flex;
	flex-direction: column;
	padding: 16px 0px;
`

export default FinalTotal;
